export default {
	data() {
		return {
			imgList: [],
			reportName:'',
			currentIndex:0
		};
	},
	onLoad(e) {
		console.log();
		this.type = e.type
		this.id = e.id?e.id:''
		this.init()
		// doctorReportAdd
		// checkupReportAdd
	},
	methods: {
		init(){
			if(this.id){
				switch (this.type){
					case 'doctorReport':
						this.findDoctorReport()
						break;
					case 'checkupReport':
						this.findCheckupReport()
						break;
				}
			}
		},
		change(e){
			this.currentIndex = e.detail.current
		},
		findDoctorReport(){
			this.$api.findDoctorReport({
				id:this.id
			}).then(res=>{
				if (res.status === 'OK') {
					this.imgList =JSON.parse(res.data.files)
					this.reportName = res.data.name
				} 
			}).catch(err=>{
				console.log(err);
			})
		},
		findCheckupReport(){
			this.$api.findCheckupReport({
				id:this.id
			}).then(res=>{
				if (res.status === 'OK') {
					this.imgList =JSON.parse(res.data.files)
					this.reportName = res.data.name
				} 
			}).catch(err=>{
				console.log(err);
			})
		},
		

	},
}
